<script>
import firebase from "firebase/app";
import "firebase/database";

var config = {
  apiKey: "AIzaSyBU7CL_rsDnh96zYBpPS0yXPBXWBGPG4-k",
  authDomain: "wikode-d85b6.firebaseapp.com",
  databaseURL: "https://wikode-d85b6-default-rtdb.firebaseio.com",
  projectId: "wikode-d85b6",
  storageBucket: "wikode-d85b6.appspot.com",
  messagingSenderId: "696118844197",
  appId: "1:696118844197:web:ca40b3990a3dd5d462d8cc",
  measurementId: "G-DQNNH2LPFF",
};

// Initialize Firebase
firebase.initializeApp(config);
//firebase.analytics();

const database = firebase.database();
const commandsRef = database.ref("commands");

export default {
  data() {
    return {
      commands: [],
    };
  },
  created() {
    this.getCommands('git');
  },
  methods: {
    getCommands(topic = null) {
      commandsRef.get().then((response) => {
        const commands = [];
        response.forEach(data => {
          const command = data.val();
          if (!topic || command.topic === topic) {
            commands.push(command);
          }
        });
        this.commands = commands;
      });
    }
  },
};
</script>

